import { Component, OnInit } from '@angular/core';
import {Http, Jsonp} from "@angular/http";
import {Router} from "@angular/router";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
   user_info={
     user_img:'',
     user_name:''
   };
  constructor(private http:Http, private jsonp:Jsonp,private router:Router) { }

  ngOnInit() {
    this.http.post("/api/get_current_userinfo", "")
      .map(response => response.json()).subscribe(
      data => {
        if(data.code==200&&JSON.stringify(data.result)!='{}'){
          this.user_info=data.result;
        }
        else if(data.code==502){

          this.router.navigate(['/login']);
        }else{
          alert('获取当前用户信息失败');
        }


      }
    )
  }
  signOut(){
    this.http.post("angular/login/user_sign_out", "")
      .map(response => response.json()).subscribe(
      data => {
        if(data.code==200&&JSON.stringify(data.result)!='{}'){
          this.router.navigate(['/login']);
        }
        else if(data.code==502){

          //window.location.href='/login';
        }else{
          alert('退出失败');
        }


      }
    )
  }
  postBill(){
    this.http.post("me/get_all_user_bill", "")
      .map(response => response.json()).subscribe(
      data => {
        if(data.code==200&&JSON.stringify(data.result)!='{}'){
          alert(' 分发成功');
        }else if(data.code==501){
          alert('本周账单已分发');
        }
        else if(data.code==502){
          this.router.navigate(['/login']);
          //window.location.href='/login';
        }else{
          alert('分发失败');
        }


      }
    )
  }

}



