import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { HttpModule,JsonpModule } from '@angular/http';//数据请求模块
import { FormsModule , ReactiveFormsModule} from '@angular/forms';//表单模块
import {HashLocationStrategy, LocationStrategy} from '@angular/common';//添加后会在路由中加入#

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { MenuComponent } from './menu/menu.component';
import { FooterComponent } from './footer/footer.component';
import { ContentComponent } from './content/content.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { DinnerComponent } from './dinner/dinner.component';
import { DinnerManageComponent } from './order-dinner/dinner-manage/dinner-manage.component';
import { DinnerFilterPipe } from './order-dinner/dinner-filter.pipe';
import { Page404Component } from './page404/page404.component';
import { DinnerStatisticsComponent } from './order-dinner/dinner-statistics/dinner-statistics.component';
import { UserDinnerListPipe } from './pipe/user-dinner-list.pipe';
import { InviteCodeComponent } from './manage/invite-code/invite-code.component';
import { LoginComponent } from './login/login.component';
import { ManageComponent } from './manage/manage.component';
import { UserBillComponent} from './user-bill/user-bill.component' ;
import { BillJsonPipe } from './pipe/bill-json.pipe';
import { BillStatusPipe } from './user-bill/bill-status.pipe';
import { ScratchCardComponent } from './scratch-card/scratch-card.component';
import {ChangeMenuService} from "./shared/change-menu.service";
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    MenuComponent,
    FooterComponent,
    ContentComponent,
    SidebarComponent,
    DinnerComponent,
    DinnerManageComponent,
    Page404Component ,
    DinnerStatisticsComponent ,
    UserDinnerListPipe ,
    InviteCodeComponent ,
    LoginComponent,
    UserBillComponent,
    BillJsonPipe,
    BillStatusPipe ,
    ScratchCardComponent,
    ManageComponent,
    DinnerFilterPipe
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    HttpModule,
    JsonpModule,
    ReactiveFormsModule
  ],
  providers: [{ provide: LocationStrategy, useClass: HashLocationStrategy, },ChangeMenuService],
  bootstrap: [AppComponent]
})
export class AppModule { }



