import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'billStatus'
})
export class BillStatusPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    var tip_txt:String='';
    if(value==1){
      tip_txt='尚未交纳'
    }else{
      tip_txt='已经交纳'
    }
    return tip_txt;
  }

}



