import {Component, OnInit, Output,EventEmitter} from '@angular/core';
import {Http, Jsonp, Headers} from "@angular/http";
import {Router} from "@angular/router";
import {ChangeMenuService} from "../shared/change-menu.service";


@Component({
  selector: 'app-user-bill',
  templateUrl: './user-bill.component.html',
  styleUrls: ['./user-bill.component.css']
})
export class UserBillComponent implements OnInit {
  user_bill_list:any=[];
  all_user_money:number=0;
  all_user_bill_list:any=[];
  all_notpay_user:number=0;
/*  @Output()
  changeMenu:EventEmitter<String>=new EventEmitter();*/
  constructor(private http:Http, private jsonp:Jsonp,private router:Router,public changeMenu:ChangeMenuService) {
    this.getUserBillList();
    this.getAllUserNotBill();
  }

  ngOnInit() {
    //this.changeMenu.emit("zhangdan");
    console.log(this.changeMenu.changeMenu('zhangdan'));
  }
  getAllUserNotBill(){
    var self:any=this;
    this.http.post('/me/get_all_user_bill_list','').map(res=>res.json()).subscribe(data=>{
      if(data.code==200){

        data.result.forEach(function(item,idx){
          self.all_notpay_user+=parseInt(item.money);
        });
        this.all_user_bill_list=data.result
      }else if(data.code==502){
        this.router.navigate(['/login']);
      }else{
        alert('获取数据失败');
      }
    })
  }
  getUserBillList(){
    var self=this;
    this.http.post("/me/get_user_bill_list", '')
      .map(response => response.json()).subscribe(
      data => {
        if(data.code==200){
          this.user_bill_list=data.result;
          data.result.forEach(function(item,idx){

            self.all_user_money+=parseInt(item.money);
          })
        }else if(data.code==502){

          this.router.navigate(['/login']);
        }else{
          alert('获取数据失败');
        }

      }
    )
  }
}



